/**
 *  <code>shaderVariable</code> shader
 */
export class ShaderVariable {
	static pointID: number = 0;
	/**@internal */
	name: string;
	/**@internal */
	type: number;
	/**@internal */
	location: number;
	/**@internal */
	isArray: boolean;
	/**@internal */
	textureID: number;
	/**@internal */
	dataOffset: number;

	/**@internal */
	caller: any;
	/**@internal */
	fun: any;
	/**@internal */
	uploadedValue: any[];
	/**@internal */
	onID: number;

	/**
	 *  <code>shaderVariable</code> 
	 */
	constructor() {
		this.onID = ShaderVariable.pointID++;
		this.textureID = -1;
	}

}


