
/**
 * @en Animation condition types
 * @zh 
 */
export enum AniConditionType {
    /**
     * @en Greater than
     * @zh 
     */
    Greater,
    /**
     * @en Less than
     * @zh 
     */
    Less,
    /**
     * @en Equal to
     * @zh 
     */
    Equals,
    /**
     * @en Not equal to
     * @zh 
     */
    NotEqual,//
}

/**
 * @en 2D animation types
 * @zh 2D
 */
export class Animation2DCondition {
    /**
     * @en ID number
     * @zh id
     */
    id: number;
    /**
     * @en Animation conditions
     * @zh 
     */
    type: AniConditionType;
    /**
     * @en Detection value
     * @zh 
     */
    checkValue: any
}