/**
 * @en Animation update modes
 * @zh 
 */
export enum AnimatorUpdateMode {
    /**
     * @en Normal update mode
     * @zh 
     */
    Normal = 0,
    /**
     * @en Low frame rate update mode
     * @zh 
     */
    LowFrame = 1,
    /**
     * @en Do not update thes animations based on time
     * @zh 
     */
    UnScaleTime = 2
}
