/**
 * @private
 */
export class System {
    /**
     * 
     * @param	name 
     * @param	classObj 
     */
    //TODO:coverage
    static changeDefinition(name: string, classObj: any): void {
        (window as any).Laya[name] = classObj;
        var str: string = name + "=classObj";
        window['eval'](str);
    }
}

