import { Context } from "../../renders/Context";
import { FontInfo } from "../../utils/FontInfo";
import { Pool } from "../../utils/Pool";
import { WordText } from "../../utils/WordText";
import { ILaya } from "../../../ILaya";
import { Const } from "../../Const";
import { ClassUtils } from "../../utils/ClassUtils";
import { Config } from "../../../Config";
import { Rectangle } from "../../maths/Rectangle";

/**
 * @en Draw text command
 * @zh 
 */
export class FillTextCmd {
    /**
     * @en Identifier for the FillTextCmd
     * @zh 
     */
    static ID: string = "FillText";

    /**
     * @en The x position of the start of the text (relative to the canvas).
     * @zh  x 
     */
    x: number = 0;
    /**
     * @en The y position of the start of the text (relative to the canvas).    
     * @zh  y 
     */
    y: number = 0;

    private _text: string;
    private _wordText: WordText;
    private _font: string;
    private _color: string;
    private _strokeColor: string = '#000000';
    private _stroke: number;
    private _align: number;
    private _fontObj: FontInfo;

    private _loosyBound:Rectangle=null;
    /**
     * @en Text content
     * @zh 
     */
    get text() {
        return this._text;
    }
    set text(value: string) {
        this._text = value;
    }

    /**
     * @en Stroke color
     * @zh 
     */
    get strokeColor() {
        return this._strokeColor;
    }
    set strokeColor(value: string) {
        this._strokeColor = value;
    }

    /**
     * @en Stroke width
     * @zh 
     */
    get stroke() {
        return this._stroke;
    }
    set stroke(value: number) {
        this._stroke = value;
    }

    /**
     * @en Text alignment
     * @zh 
     */
    get align() {
        return this._align;
    }
    set align(value: number) {
        this._align = value;
    }

    /**
     * @en Create a FillTextCmd instance
     * @param text Text content
     * @param x X position
     * @param y Y position
     * @param font Font
     * @param color Text color
     * @param align Alignment
     * @param stroke Stroke width
     * @param strokeColor Stroke color
     * @returns FillTextCmd instance
     * @zh 
     * @param text 
     * @param x x
     * @param y y
     * @param font 
     * @param color 
     * @param align 
     * @param stroke 
     * @param strokeColor 
     * @returns 
     */
    static create(text: string | WordText | null, x: number, y: number, font: string, color: string | null, align: string, stroke: number, strokeColor: string | null): FillTextCmd {
        var cmd: FillTextCmd = Pool.getItemByClass("FillTextCmd", FillTextCmd);
        cmd._text = null;
        cmd._wordText = null;
        cmd.x = x;
        cmd.y = y;
        cmd.font = font;
        cmd.color = color;
        cmd._stroke = stroke;
        cmd._strokeColor = strokeColor;

        switch (align) {
            case 'center':
                cmd._align = Const.ENUM_TEXTALIGN_CENTER;
                break;
            case 'right':
                cmd._align = Const.ENUM_TEXTALIGN_RIGHT;
                break;
            default:
                cmd._align = Const.ENUM_TEXTALIGN_DEFAULT;
        }

        if (text instanceof WordText) {
            cmd._wordText = text;
            text.cleanCache();
        }
        else
            cmd._text = text;

        return cmd;
    }

    /**
     * @en Recycle to the object pool
     * @zh 
     */
    recover(): void {
        Pool.recover("FillTextCmd", this);
    }

    /**
     * @private
     * @en Execute the drawing text command
     * @param context The rendering context
     * @param gx Global X offset
     * @param gy Global Y offset
     * @zh 
     * @param context 
     * @param gx X
     * @param gy Y
     */
    run(context: Context, gx: number, gy: number): void {
        if (ILaya.stage.isGlobalRepaint()) {
            this._wordText && this._wordText.cleanCache();
        }
        if (null == this._text) this._text = '';
        if (null == this._fontObj) {
            this.font = null;
        }
        if (null == this._color) {
            this._color = '#ffffff';
        }

        context._fast_filltext(this._wordText || this._text, this.x + gx, this.y + gy, this._fontObj, this._color, this._strokeColor, this._stroke, this._align);
    }

    /**
     * @private
     * @en The identifier for the FillTextCmd
     * @zh 
     */
    get cmdID(): string {
        return FillTextCmd.ID;
    }

    /**
     * @en Define the font size and font, e.g., "20px Arial".
     * @zh "20px Arial"
     */
    get font(): string {
        return this._font;
    }

    set font(value: string) {
        this._font = value;
        if (!value)
            value = Config.defaultFontSize + "px " + Config.defaultFont;
        this._fontObj = FontInfo.parse(value);
        this._wordText && this._wordText.cleanCache();
    }

    /**
     * @en Define the text color, e.g., "#ff0000".
     * @zh "#ff0000"
     */
    get color(): string {
        return this._color;
    }

    set color(value: string) {
        this._color = value;
        this._wordText && this._wordText.cleanCache();
    }



    getBoundPoints(sp?: { width: number, height?: number }): number[] {
        if(!this._loosyBound){
            let ctx = ILaya.Browser.context;
            ctx.save();
            ctx.font = this.font;
            let size = ctx.measureText(this.text);
            let w = size.width;
            ctx.restore();
            let x = this.x;
            let y = this.y;

            switch(this._align){
                case Const.ENUM_TEXTALIGN_CENTER:
                    x-=w/2;
                break;
                case Const.ENUM_TEXTALIGN_RIGHT:
                    x-=w;
                break;
                default:
                break;
            }
            //
            x-=4;
            y-=this._fontObj._size;            
            this._loosyBound = new Rectangle(x,y,w+8,this._fontObj._size*2);
        }

        return Rectangle._getBoundPointS(this._loosyBound.x, this._loosyBound.y, this._loosyBound.width, this._loosyBound.height, null)
    }

}

ClassUtils.regClass("FillTextCmd", FillTextCmd);