import { Event } from "../events/Event"
import { EventDispatcher } from "../events/EventDispatcher"
import { Browser } from "../utils/Browser"
import { Byte } from "../utils/Byte"

/**
 * 
 * @eventType Event.OPEN
 * */
/*[Event(name = "open", type = "laya.events.Event")]*/
/**
 * 
 * @eventType Event.MESSAGE
 * */
/*[Event(name = "message", type = "laya.events.Event")]*/
/**
 * 
 * @eventType Event.CLOSE
 * */
/*[Event(name = "close", type = "laya.events.Event")]*/
/**
 * 
 * @eventType Event.ERROR
 * */
/*[Event(name = "error", type = "laya.events.Event")]*/

/**
 * <p> <code>Socket</code>  HTML5 WebSocket full-duplex Browser/Client Agent </p>
 * <p> <code>Socket</code>  <code>new Socket</code>  <code>Socket</code>  <code>Socket</code> </p>
 */
export class Socket extends EventDispatcher {
    /**
     * <p> CPU </p>
     * <p> LITTLE_ENDIAN </p>
     * <p> BIG_ENDIAN </p>
     */
    static LITTLE_ENDIAN: string = "littleEndian";
    /**
     * <p> CPU </p>
     * <p> BIG_ENDIAN </p>
     * <p> LITTLE_ENDIAN </p>
     */
    static BIG_ENDIAN: string = "bigEndian";
    /**@internal */
    _endian: string;
    /**@private */
    protected _socket: any;
    /**@private */
    private _connected: boolean;
    /**@private */
    private _addInputPosition: number;
    /**@private */
    private _input: any;
    /**@private */
    private _output: any;

    /**
     * true
     */
    disableInput: boolean = false;
    /**
     *  <code>Byte</code> 
     */
    private _byteClass: new () => any;
    /**
     * <p> connect  connectByUrl </p>
     * <p> Event.ERROR </p>
     * @see https://html.spec.whatwg.org/multipage/comms.html#dom-websocket
     */
    protocols: any = [];

    /**
     * 
     */
    get input(): any {
        return this._input;
    }

    /**
     * 
     */
    get output(): any {
        return this._output;
    }

    /**
     *  Socket 
     */
    get connected(): boolean {
        return this._connected;
    }

    /**
     * <p> CPU </p>
     * <p> LITTLE_ENDIAN </p>
     * <p> BIG_ENDIAN </p>
     */
    get endian(): string {
        return this._endian;
    }

    set endian(value: string) {
        this._endian = value;
        if (this._input != null) this._input.endian = value;
        if (this._output != null) this._output.endian = value;
    }

    /**
     * <p> Socket  Socket.BIG_ENDIAN </p>
     * @param host		
     * @param port		
     * @param byteClass	 Byte  null  Byte  Byte 
     * @param protocols	
     * @param isSecure  WebSocketwssfalsews
     * @see laya.utils.Byte
     */
    constructor(host: string | null = null, port: number = 0, byteClass: new () => any = null, protocols: any[] | null = null, isSecure: boolean = false) {
        super();
        this._byteClass = byteClass ? byteClass : Byte;
        this.protocols = protocols;
        this.endian = Socket.BIG_ENDIAN;
        if (host && port > 0 && port < 65535) this.connect(host, port, isSecure);
    }

    /**
     * <p></p>
     * <p> Event.OPEN  Event.ERROR  Event.CLOSE  Event.MESSAGE   Event.MESSAGE  HTML DOM Event </p>
     * @param host	
     * @param port	
     * @param isSecure  WebSocketwssfalsews
     */
    connect(host: string, port: number, isSecure: boolean = false): void {
        this.connectByUrl(`${isSecure ? "wss" : "ws"}://${host}:${port}`);
    }

    /**
     * <p> WebSocket URL URL  ws://yourdomain:port</p>
     * <p> Event.OPEN  Event.ERROR  Event.CLOSE  Event.MESSAGE   Event.MESSAGE  HTML DOM Event </p>
     * @param url	 WebSocket URL URL  ws://yourdomain:port
     */
    connectByUrl(url: string): void {
        if (this._socket != null) this.close();

        this._socket && this.cleanSocket();

        if (!this.protocols || this.protocols.length == 0) {
            this._socket = new Browser.window.WebSocket(url);
        } else {
            this._socket = new Browser.window.WebSocket(url, this.protocols);
        }

        this._socket.binaryType = "arraybuffer";

        this._output = new this._byteClass();
        this._output.endian = this.endian;
        this._input = new this._byteClass();
        this._input.endian = this.endian;
        this._addInputPosition = 0;

        this._socket.onopen = (e: any) => {
            this._onOpen(e);
        };
        this._socket.onmessage = (msg: any): void => {
            this._onMessage(msg);
        };
        this._socket.onclose = (e: any): void => {
            this._onClose(e);
        };
        this._socket.onerror = (e: any): void => {
            this._onError(e);
        };
    }

    /**
     * SocketSocketSocket
     */
    cleanSocket(): void {
        this.close();
        this._connected = false;
        this._socket.onopen = null;
        this._socket.onmessage = null;
        this._socket.onclose = null;
        this._socket.onerror = null;
        this._socket = null;
    }

    /**
     * 
     */
    close(): void {
        if (this._socket != null) {
            try {
                this._socket.close();
            } catch (e) {
            }
        }
    }

    /**
     * @private
     *  
     */
    protected _onOpen(e: any): void {
        this._connected = true;
        this.event(Event.OPEN, e);
    }

    /**
     * @private
     * 
     * @param msg 
     */
    protected _onMessage(msg: any): void {
        if (!msg || !msg.data) return;
        var data: any = msg.data;
        if (this.disableInput && data) {
            this.event(Event.MESSAGE, data);
            return;
        }
        if (this._input.length > 0 && this._input.bytesAvailable < 1) {
            this._input.clear();
            this._addInputPosition = 0;
        }
        var pre: number = this._input.pos;
        !this._addInputPosition && (this._addInputPosition = 0);
        this._input.pos = this._addInputPosition;

        if (data) {
            if (typeof (data) == 'string') {
                this._input.writeUTFBytes(data);
            } else {
                this._input.writeArrayBuffer(data);
            }
            this._addInputPosition = this._input.pos;
            this._input.pos = pre;
        }
        this.event(Event.MESSAGE, data);
    }

    /**
     * @private
     * 
     */
    protected _onClose(e: any): void {
        this._connected = false;
        this.event(Event.CLOSE, e)
    }

    /**
     * @private
     * 
     */
    protected _onError(e: any): void {
        this.event(Event.ERROR, e)
    }

    /**
     * 
     * @param	data StringArrayBuffer
     */
    send(data: any): void {
        this._socket.send(data);
    }

    /**
     * 
     */
    flush(): void {
        if (this._output && this._output.length > 0) {
            var evt: any;
            try {
                this._socket && this._socket.send(this._output.__getBuffer().slice(0, this._output.length));
            } catch (e) {
                evt = e;
            }
            this._output.endian = this.endian;
            this._output.clear();
            if (evt) this.event(Event.ERROR, evt);
        }
    }
}


